import React,{useState} from 'react';
import {Button} from 'react-bootstrap';
import './style.css';
import Validation from './validation';
import axios from 'axios';

function Signin() {  

  const [values, setValues] = useState({
    name: "",
    email: "",
    password: ""
  });

  const [errors, setErrors] = useState({});

  const handleChange = (event) =>{
       setValues({
         ...values,
         [event.target.name] : [event.target.value]
       });
  };

axios.get("http://loacalhost:4000/app/register")
.then(response=> console.log(response.data));


  const handleFormSubmit = (event) => {
    event.preventDefault();
    setErrors(Validation(values));

  };

  return (
    <div className="App">
      <div className="container">
        <form className="register-form">
        <h3 className="title">Register</h3>
        <label>Name: </label> 
            <input type = "text" className="form-control" name="name" value={values.name} onChange={handleChange}/> 
            {errors.name && <p className="error">{errors.name}</p>}
            <label>Email: </label> 
            <input type = "text" className="form-control" name="email" value={values.email} onChange={handleChange}/> 
            {errors.email && <p className="error">{errors.email}</p>}
            <label>Password: </label> 
            <input type = "pass" className="form-control" name="password" value={values.password} onChange={handleChange}/> 
            {errors.password && <p className="error">{errors.password}</p>}

            <div className="form-group">
            <Button type="submit" className="signin" onClick={handleFormSubmit}>
        Register
           
         </Button>
           <span className="already"><a href="#">Already have an account, Signin</a></span>
         </div>
        </form> 
      </div>
    </div>
  );
}

export default Signin;